import React from 'react'
import { withRouter } from 'react-router-dom'
import TableBar from './TableBar'

const Xq = (props) => {
    return (
        <div>
            <img
                src={props.location.src}
                alt=""
                style={{width: '100%', 
                        position:'absolute',
                        top:0,                 
                        left: 0,
                }}
            />
            
            <p style={{color:'black',position:'absolute',top:'390px',zIndex:'1'}}>{props.location.title}</p>
            <p style={{color:'black',position:'absolute',top:'405px',zIndex:'1'}}>¥{props.location.price}</p>
            <p 
                style={{
                    zIndex:'98',
                    top:'120px',                 
                    marginLeft: '20px',
                    backgroundColor:'black',
                    opacity:0.3, 
                    color:'white',
                    height:'30px',
                    width:'30px',
                    borderRadius:'30px',
                    fontSize:'25px',
                    textAlign:'center',
                }}
                onClick={ () => {
                    props.history.push('/Home');
                } }
            > &lt; </p>
            
            <TableBar value={props.location.value}/>
            
        </div>
    )
}

export default withRouter(Xq)
