import { NavBar,Icon, SearchBar } from 'antd-mobile'
import React, { useEffect } from 'react'
import { Carousel, WingBlank } from 'antd-mobile';
import Search from './Search';
import { withRouter } from 'react-router-dom';
import Mytab from './Mytab';
import { connect } from 'react-redux';
import {getDate} from './actionCreators'


class Home extends React.Component {
  constructor(props){
    super(props);
    
  };
  
  state = {
    data: ['1', '2', '3','4','5','6','7','8'],
    imgHeight: 176,
  }
  
  componentDidMount() {
    setTimeout(() => {
      this.setState({
      data: ['picasso.alicdn.com/imgextra/i3/382208/O1CNA1iyU1IP100ff4af62860b14de730004_!!382208-0-picassobanner',
        'gw.alicdn.com/imgextra/i1/161/O1CN01d4bd8s1D3mOLAJt74_!!161-0-lubanu',
        'img.alicdn.com/imgextra/i4/6000000001122/O1CN01h1tZ5p1K9v0sfGQ4D_!!6000000001122-0-octopus',
        'img.alicdn.com/simba/img/TB10wzobQ9l0K4jSZFKSutFjpXa',
        'aecpm.alicdn.com/simba/img/TB1sv_jkQ9l0K4jSZFKSutFjpXa',
        'img.alicdn.com/simba/img/TB18X1VSoY1gK0jSZFMSuuWcVXa',
        'img.alicdn.com/imgextra/i2/6000000006053/O1CN01HffIb91uaK0uSiWvR_!!6000000006053-0-octopus',
        'gw.alicdn.com/imgextra/i4/41/O1CN01lMI3m21CAoruPKN9y_!!41-0-ppp-picassogw'],
      
    });
    }, 100);
    
  }
  render(){
   
    this.props.dispatch(getDate())
    return (
      <div>
        <NavBar
        mode="light"
        style={{backgroundColor:'rgb(255,125,42)'}}
        icon={<i className='iconfont icon-taobao' style={{color:'rgb(255,78,34)'}}/>}
        rightContent={[
          <div  
            style={{ 
              marginLeft: '-80px',
              width:'300px',
              height:'25px',
              backgroundColor:'rgb(255,78,34)' ,
            }}
            onClick={ () => {
              this.props.history.push('/Search');
            }}
          >
            <i className='iconfont icon-search' style={{color:'white',marginLeft:'80px',marginTop:'15px'}} />
            <p style={{marginTop:'-15px',marginLeft:'100px',color:'white'}}>寻找宝贝店铺</p>
           
          </div>
        ]}
        >
          
        </NavBar>
        
        <Carousel
        autoplay
        infinite
        >
        {this.state.data.map(val => (
          <a
            key={val}
            href="https://h5.m.taobao.com/applink/smb-fid-sender.html"
            style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
          >
            <img
              src={`https://${val}.jpg`}
              alt=""
              style={{ width: '100%', verticalAlign: 'top' }}
              onLoad={() => {
                window.dispatchEvent(new Event('resize'));
                this.setState({ imgHeight: 'auto' });
              }}
            />
          </a>
        ))}
        </Carousel>
        <div style={{marginBottom:'50px'}}>
          
            {this.props.list.map(val1 => (
              <div style={{
                display:'inline-block',
                marginLeft:'25px',
                marginTop:'25px',
                backgroundColor:'white',
                border:'1px solid white',
                }}>
                <a>
                  <img
                  src={val1.img}
                  alt=""
                  style={{  verticalAlign: 'top' }}
                  style={{width: '150px',height:'150px'}}
                  onClick={ () => {
                    this.props.history.push({
                      pathname:'/Xq',
                      id:val1.id,
                      src:val1.img,
                      title:val1.title,
                      price:val1.price,
                      value:val1,
                    });
                  } }
                />              
                </a>
                <p>{val1.title}</p>
                <p>{val1.price}</p>
              </div>
            ))}
        </div>
        <Mytab/>
           

      </div>
      )
  }
    
    
}
const mapStateToProps = (state) => ({
  list: state.datalist,
})

export default connect(mapStateToProps)(withRouter(Home));