import React from 'react';
import { TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>

                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="black"
                    barTintColor="white"
                    
                >
                    <TabBar.Item                       
                        icon={
                            <i className='iconfont icon-home1'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-home2'></i>                           
                        }
                        title="首页"
                        key="home"
                        selected={ this.props.location === '/Home' }
                        onPress={ () => {
                            this.props.history.push('/Home');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-favor'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang'></i>                           
                        }
                        title="收藏"
                        key="cart"
                        selected={ this.props.location === '/Car' }
                        onPress={ () => {
                            if(this.props.username){
                                this.props.history.push('/Car')
                            }else{
                                this.props.history.push('/login')
                            }
                            
                        } }
                        data-seed="logId1"
                    >

                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdan2'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-dingdan1'></i>
                        }
                        title="订单列表"
                        key="Friend"
                        selected={ this.props.location === '/List' }
                        onPress={ () => {
                           
                            this.props.history.push('/List')
                        } }
                    >                       
			        </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-wode'></i> }
                        selectedIcon={<i className='iconfont icon-wode1'></i> }
                        title="我的淘宝"
                        key="my"
                        selected={ this.props.location === '/My' }
                        onPress={ () => {
                            this.props.history.push('/My')                          
                        } }
                    >                       
			        </TabBar.Item>
                   
                </TabBar>
            </div>
        );
    }
}
const mapStateToProps = (state) => ({
    username:state.login.username
})
  
export default connect(mapStateToProps)(withRouter(Mytab));
