import { TabBar } from 'antd-mobile'
import React, { Component } from 'react'
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom'
import {shoucang} from './actionCreators'

class TableBar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0,
                backgroundColor:'white',
                height:'60px'
            } }>
            <div style={{width:'50px',marginLeft:'20px',float:'left',marginTop:'20px'}}>
                <span className='iconfont icon-shop'></span>
                <span style={{}}>店铺</span>
            </div>
            <div style={{width:'50px',float:'left',marginTop:'20px'}}>
                <span className='iconfont icon-wang'></span>
                <span>客服</span>
            </div>
            <div style={{width:'50px',float:'left',marginTop:'20px'}} 
                className='sc'
                onClick={()=>{
                    let n =document.getElementsByClassName('sc')[0];
                    n.firstChild.className='iconfont icon-shoucang'
                    this.props.dispatch({
                        type:'SHOUCANG',
                        sclist:this.props.value
                    })
                    
                }}
            >
                <span className='iconfont icon-favor'></span>
                <span>收藏</span>
                
            </div>
            <div 
                style={{
                    borderTopLeftRadius:'15px',
                    borderBottomLeftRadius:'15px',
                    backgroundColor:'rgb(255,125,42)',
                    width:'100px',
                    height:'30px',                   
                    color:'white',
                    fontWeight:'700',
                    float:'left',
                    marginTop:'15px',
                    textAlign:'center',
                    lineHeight:'0px',
            }}>
                <p>加入购物车</p>
            </div>
            <div 
                style={{
                    width:'100px',
                    height:'30px',
                    backgroundColor:'rgb(255,78,34)',
                    borderTopRightRadius:'15px',
                    borderBottomRightRadius:'15px',
                    color:'white',
                    fontWeight:'700',
                    float:'left',
                    marginTop:'15px',
                    textAlign:'center',
                    lineHeight:'0px',
            }}>
                <p >立即购买</p>
            </div>
        </div>

               
        
        );
    }
}
const mapStateToProps = (state) => ({
    sclist: state.sclist
})
  
export default connect(mapStateToProps)(withRouter(TableBar));
