import React from 'react'
import { connect } from 'react-redux'
import {login} from './actionCreators'

const log = (props) => {
    console.log(props);
    return (
        <div>
            <div>请登录</div>
            <form style={{marginLeft:'100px', marginTop:'80px'}}>
                <input type='text' placeholder='请输入用户名'></input><br></br>
                <input type='password' placeholder='请输入密码'></input>
            </form>
            <button
                style = {{marginLeft:'150px',marginTop:'50px'}}
                onClick={()=>{
                    props.dispatch(login());
                    props.history.push('/car')
                }}
            >登录</button>
        </div>
    )
}

  
  export default connect()(log);


