import { SearchBar } from 'antd-mobile'
import React from 'react'
import { withRouter } from 'react-router-dom'

const Search = (props) => {
    return (
        <div>
            <SearchBar 
                placeholder='宝贝'
                showCancelButton='true'
                onClear={ () => {
                    props.history.goBack('/Home');
                }}
            >
            </SearchBar>
        </div>
    )
}

export default withRouter(Search);
